import React from 'react';
import { HashRouter as Router, Redirect, Route, Switch } from "react-router-dom";
import './App.css';
import CV from './CV';

function App() {
  return (
    <Router>
      <div>
        <Switch>
          <Route exact path="/">
            <CV/>
          </Route>
          <Route exact path="/cv">
            <CV/>
          </Route>
          <Route path="*">
            <NoMatch/>
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

function NoMatch() {
  return (
    <Route
      render={({ location }) => (
        <Redirect
          to={{
            pathname: "/",
            state: { from: location }
          }}
        />)}
    />
  );
}

export default App;
